          aria-hidden="true"
        ></div>
      </div>
      <section v-else>
        <h4>หลักสูตร / คอร์สเรียนของฉัน</h4>
        <hr />
        <!-- when have data. -->
        <div
          v-if="
            this.my_subject_leassons != '' ||
            this.my_subject_leassons_left != ''
          "          
        >
          <div class="float-right mb-2 ">
            <div class="input-group">
              <div class="form-outline" style="padding-right:5px">
                <input
                  type="search"
                  id="form1"
                  placeholder="Search"
                  class="form-control"
                />
              </div>
              <button type="button" class="btn btn-primary">
                <i class="fas fa-search"></i>
              </button>
            </div>
          </div>
          <div class="row">
            <div class="col-md-8">
              <div>
                <div
                  class="mb-2"
                  v-for="lesson in my_subject_leassons"
                  :key="lesson.id"
                >
                  <div class="card p-3">
                    <div class="mb-0 p-3 text-left">
                      <i class="fa fa-leanpub" aria-hidden="true"></i>
                      <span class="text-danger"><b>แบบประสานเวลา</b></span>
                    </div>
                    <div class="row">
                      <div class="col-md-3">
                        <span
                          ><img
                            width="100px"
                            height="100px"
                            class="border border-dark rounded ml-3"
                            :src="lesson.image"
                            alt=""
                        /></span>
                      </div>
                      <div class="col-md-6">
                        <p>
                          รหัสวิชา:
                          <b
                            ><u>{{ lesson.subjectId }}</u></b
                          >
                        </p>
                        <p>ชื่อวิชา: {{ lesson.subjectName }}</p>
                      </div>
                      <div class="col-md">
                        <div class="p-2 text-right">
                          <div class="dropup">
                            <a
                              id="dropdownMenuButton"
                              data-toggle="dropdown"
                              aria-haspopup="true"
                              aria-expanded="false"
                              class="btn btn-primary"
                              ><i class="fa fa-cog" aria-hidden="true"></i
                            ></a>
                            <ul
                              class="dropdown-menu"
                              aria-labelledby="dropdownMenuButton"
                            >
                              <li>
                                <div v-if="showStart == 0">
                                  <a
                                    v-if="lesson.student_status == 1"
                                    class="dropdown-item disabled"
                                    >เริ่มเรียน</a
                                  >
                                  <a
                                    v-else
                                    class="dropdown-item disabled"
                                    href="#"
                                    >รอการยืนยัน</a
                                  >
                                </div>
                                <div v-else>
                                  <a
                                    v-if="lesson.student_status == 1"
                                    class="dropdown-item"
                                    ><router-link
                                      style="text-decoration: none; color: black"
                                      :to="{
                                        name: 'preface2',
                                        params: { id: lesson.id },
                                      }"
                                      >เริ่มเรียน</router-link
                                    ></a
                                  >
                                  <a
                                    v-else
                                    class="dropdown-item disabled"
                                    href="#"
                                    >รอการยืนยัน</a
                                  >
                                </div>
                              </li>
                              <li>
                                <a class="dropdown-item"
                                  ><router-link
                                    style="text-decoration: none; color: black"
                                    :to="{
                                      name: 'lessons_id',
                                      params: { id: lesson.id },
                                    }"
                                    >ดูหลักสูตร</router-link
                                  ></a
                                >
                              </li>
                              <li>
                                <a class="dropdown-item disabled" href="#"
                                  >ใบประกาศ(PDF)</a
                                >
                              </li>
                              <li><hr class="dropdown-divider" /></li>
                              <li>
                                <a
                                  class="dropdown-item"
                                  @click="destroy_subject(lesson.class_id)"
                                  >ยกเลิกหลักสูตร</a
                                >
                              </li>
                            </ul>
                          </div>
                        </div>
                      </div>
                    </div>
                  </div>
                </div>
                <div v-for="lesson in my_subject_leassons_left" :key="lesson.id">
                  <div class="card p-3 mb-2">
                    <div class="mb-0 p-3 text-left">
                      <i class="fa fa-leanpub" aria-hidden="true"></i>
                      <span class="text-danger"><b>แบบไม่ประสานเวลา</b></span>
                    </div>
                    <div class="row">
                      <div class="col-md-3">
                        <span
                          ><img
                            width="100px"
                            height="100px"
                            class="border border-dark rounded ml-3"
                            :src="lesson.image"
                            alt=""
                        /></span>
                      </div>
                      <div class="col-md-6">
                        <p>
                          รหัสวิชา:
                          <b
                            ><u>{{ lesson.subjectId }}</u></b
                          >
                        </p>
                        <p>ชื่อวิชา: {{ lesson.subjectName }}</p>
                      </div>
                      <div class="col-md">
                        <div class="p-2 text-right">
                          <div class="dropup">
                            <a
                              id="dropdownMenuButton"
                              data-toggle="dropdown"
                              aria-haspopup="true"
                              aria-expanded="false"
                              class="btn btn-primary"
                              ><i class="fa fa-cog" aria-hidden="true"></i
                            ></a>
                            <ul
                              class="dropdown-menu"
                              aria-labelledby="dropdownMenuButton"
                            >
                              <li>
                                <a class="dropdown-item"
                                  ><router-link
                                    style="text-decoration: none; color: black"
                                    :to="{
                                      name: 'preface2',
                                      params: { id: lesson.id },
                                    }"
                                    >เริ่มเรียน</router-link
                                  ></a
                                >
                              </li>
                              <li>
                                <a class="dropdown-item"
                                  ><router-link
                                    style="text-decoration: none; color: black"
                                    :to="{
                                      name: 'lessons_id',
                                      params: { id: lesson.id },
                                    }"
                                    >ดูหลักสูตร</router-link
                                  ></a
                                >
                              </li>
                              <li>
                                <a class="dropdown-item disabled" href="#"
                                  >ใบประกาศ(PDF)</a
                                >
                              </li>
                              <li><hr class="dropdown-divider" /></li>
                              <li>
                                <a
                                  class="dropdown-item"
                                  @click="destroy_subject(lesson.class_id)"
                                  >ยกเลิกหลักสูตร</a
                                >
                              </li>
                            </ul>
                          </div>
                        </div>
                      </div>
                    </div>
                  </div>
                </div>
              </div>
            </div>
          </div>
        </div>
        <!-- when have not data. -->
        <div class="mb-4" v-else>
          <div class="row">
            <div class="col-md-3">
              <div class="input-group">
                <div class="form-outline">
                  <input
                    type="search"
                    id="form1"
                    placeholder="Search"
                    class="form-control"
                  />
                </div>
                <button type="button" class="btn btn-primary">
                  <i class="fas fa-search"></i>
                </button>
              </div>
            </div>
            <div class="col-md-9">
              <div class="card">
                <div>
                  <div class="p-3 mb-2">
                    <div class="mb-0 p-3 text-left text-center">
                      <i class="fa fa-leanpub" aria-hidden="true"></i>
                      <span class="text-danger"><b>เลือกคอร์ดเรียน</b></span
                      ><br /><br />
                      <a href="/learning/course" class="btn btn-primary"
                        >Find!!</a
                      >
                    </div>
                  </div>
                </div>
              </div>
            </div>
          </div>
        </div>
      </section>
    </div>

    <div id="footer">
      <FooterComponent />
    </div>
  </div>
</template>

<script scoped>
import axios from "axios";
import HeaderComponent from "../../components/HeaderComponent.vue";
import FooterComponent from "../../components/FooterComponent.vue";
export default {
  name: "dashboard",
  components: { HeaderComponent, FooterComponent },
  data() {
    return {
      loading: true,
      my_subject_leassons: [],
      my_subject_leassons_left: [],
      curreentTime: "",
      showStart: 0,
    };
  },
  mounted() {
    if (this.$store.state.token !== "") {
      axios
        .get("/api/auth/user-profile/", {
          headers: {
            "Content-Type": "application/json",
            Authorization: "Bearer " + this.$store.state.token,
          },
        })
        .then((res) => {
          this.loading = false;
        })
        .catch((err) => {
          this.$store.commit("clearToken");
          this.$router.push("/learning/login");
        });
    } else {
      this.loading = false;
      this.$router.push("/learning/login");
    }
    this.curreentTime = this.currentDateTime();
    this.my_subject_leasson();
    this.my_subject_leasson_left();
  },
  methods: {
    currentDateTime() {
      const current = new Date();
      const date =
        current.getFullYear() +
        "-" +
        (current.getMonth() < 10 ? "0" : "") +
        +(current.getMonth() + 1) +
        "-" +
        (current.getDate() < 10 ? "0" : "") +
        current.getDate();
      const time =
        (current.getHours() < 10 ? "0" : "") +
        current.getHours() +
        ":" +
        (current.getMinutes() < 10 ? "0" : "") +
        current.getMinutes() +
        ":" +
        (current.getSeconds() < 10 ? "0" : "") +
        current.getSeconds();
      const dateTime = date + "T" + time;

      return dateTime;
    },
    my_subject_leasson() {
      axios
        .get("/api/my_subject_leasson/" + this.$store.state.user_id)
        .then((res) => {
          this.my_subject_leassons = res.data;
          //   console.log(this.curreentTime);
          for (let i = 0; i < res.data.length; i++) {
            if (res.data[i].student_status == 1) {
              axios
                .get("/api/showdata_time/" + this.$store.state.user_id)
                .then((res) => {
                  console.log(this.curreentTime);
                  for (let i = 0; i < res.data.length; i++) {
                    // console.log(res.data[i].start);
                    if (
                      this.curreentTime >= res.data[i].start &&
                      this.curreentTime <= res.data[i].end
                    ) {
                      this.showStart++;
                    }
                  }
                })
                .catch((err) => {
                  this.error = "Error!!";
                });
            }
          }
        })
        .catch((err) => {
          this.error = "Error!!";
        });
    },
    my_subject_leasson_left() {
      axios
        .get("/api/my_subject_leasson_left/" + this.$store.state.user_id)
        .then((res) => {
          //   console.log(res.data);
          this.my_subject_leassons_left = res.data;
        })
        .catch((err) => {
          this.error = "Error!!";
        });
    },
    destroy_subject(class_id) {
      axios
        .get("/api/destroy_subject/" + class_id)
        .then((res) => {
          this.$router.go();
        })
        .catch((err) => {
          this.error = "Error!!";
        });
    },
  },
};
</script>

<style scoped>
/* ##### >> https://fonts.google.com/specimen/Sarabun?query=sara#standard-styles */
@import url('https://fonts.googleapis.com/css2?family=Sarabun:wght@300&display=swap');

h1,h2,h3,h4,h5,h6 {
  font-family: 'Sarabun', sans-serif;
}
#content {
  margin-top: -2%;
}
#load {
  margin-top: 6%;
}
</style>